import type React from "react"
import { useState } from "react"
import { Table, TableBody, TableCell, TableHead, TableHeader, TableRow } from "@/components/ui/table"
import { Card, CardContent, CardHeader, CardTitle } from "@/components/ui/card"
import { Button } from "@/components/ui/button"
import {
    Dialog,
    DialogContent,
    DialogDescription,
    DialogHeader,
    DialogTitle,
    DialogTrigger,
} from "@/components/ui/dialog"

interface ExtractedData {
    amount: number
    transactionDate: string
    transactionNumber: string
    merchantName: string
    items: Array<{
        description: string
        quantity: number
        unitPrice: number
        totalPrice: number
    }>
    totalAmount: number
    taxAmount: number
    currency: string
}

interface ExtractedDataListProps {
    data: ExtractedData[]
}

const ExtractedDataList: React.FC<ExtractedDataListProps> = ({ data }) => {
    const [selectedVoucher, setSelectedVoucher] = useState<ExtractedData | null>(null)

    if (data.length === 0) return null

    return (
        <Card>
            <CardHeader>
                <CardTitle>Vouchers Procesados</CardTitle>
            </CardHeader>
            <CardContent>
                <Table>
                    <TableHeader>
                        <TableRow>
                            <TableHead>Número de Operación</TableHead>
                            <TableHead>Precio</TableHead>
                            <TableHead>Fecha</TableHead>
                            <TableHead>Acciones</TableHead>
                        </TableRow>
                    </TableHeader>
                    <TableBody>
                        {data.map((voucher, index) => (
                            <TableRow key={index}>
                                <TableCell>{voucher.transactionNumber}</TableCell>
                                <TableCell>
                                    {voucher.amount} {voucher.currency}
                                </TableCell>
                                <TableCell>{voucher.transactionDate}</TableCell>
                                <TableCell>
                                    <Dialog>
                                        <DialogTrigger asChild>
                                            <Button variant="outline" onClick={() => setSelectedVoucher(voucher)}>
                                                Más detalles
                                            </Button>
                                        </DialogTrigger>
                                        <DialogContent className="sm:max-w-[425px]">
                                            <DialogHeader>
                                                <DialogTitle>Detalles del Voucher</DialogTitle>
                                                <DialogDescription>Información completa del voucher procesado</DialogDescription>
                                            </DialogHeader>
                                            {selectedVoucher && (
                                                <div className="grid gap-4 py-4">
                                                    <div className="grid grid-cols-4 items-center gap-4">
                                                        <span className="font-bold">Comerciante:</span>
                                                        <span className="col-span-3">{selectedVoucher.merchantName}</span>
                                                    </div>
                                                    <div className="grid grid-cols-4 items-center gap-4">
                                                        <span className="font-bold">Monto Total:</span>
                                                        <span className="col-span-3">
                              {selectedVoucher.totalAmount} {selectedVoucher.currency}
                            </span>
                                                    </div>
                                                    <div className="grid grid-cols-4 items-center gap-4">
                                                        <span className="font-bold">Impuesto:</span>
                                                        <span className="col-span-3">
                              {selectedVoucher.taxAmount} {selectedVoucher.currency}
                            </span>
                                                    </div>
                                                    <div>
                                                        <span className="font-bold">Artículos:</span>
                                                        <ul className="list-disc list-inside mt-2">
                                                            {selectedVoucher.items.map((item, index) => (
                                                                <li key={index}>
                                                                    {item.description} - {item.quantity} x {item.unitPrice} = {item.totalPrice}{" "}
                                                                    {selectedVoucher.currency}
                                                                </li>
                                                            ))}
                                                        </ul>
                                                    </div>
                                                </div>
                                            )}
                                        </DialogContent>
                                    </Dialog>
                                </TableCell>
                            </TableRow>
                        ))}
                    </TableBody>
                </Table>
            </CardContent>
        </Card>
    )
}

export default ExtractedDataList

